<template>
  <el-dialog
    title="Api接口调试"
    :visible.sync="dialogVisible"
    :close-on-click-modal="false"
    width="1300px">
    <div
      v-loading="apiLoading"
      element-loading-text="拼命加载中"
      element-loading-spinner="el-icon-loading"
      element-loading-background="rgba(0, 0, 0, 0.8)">
      <div class="url-box">
        <span class="api-name" v-text="apiData.name"></span>
        <span class="title">请求地址:</span>
        <input class="url-value" v-model="envUrl+apiData.url" readonly placeholder="请输入内容"></input>
        <div class="select-api">
          接口切换
          <select v-model="currApiUrl" @change="changeApi">
            <optgroup v-for="(group,index) in apiGroupArr" :key="index" :label="group.groupName">
              <option v-for="(api,_index) in group.list" :key="_index" :value="api.url" v-text="api.name"
                      :selected="currApiUrl===api.url?'selected':''"></option>
            </optgroup>
          </select>
        </div>
        <div class="debug-button">
          <button class="button red" @click="request">发起请求</button>
        </div>
      </div>
      <div class="request-box" v-if="apiData.requestHeaderString.length>0">
        <div class="title">
          <span>请求头部-RequestHeader:</span>
        </div>
        <textarea class="my-textarea my-scroller header">{{ apiData.requestHeaderString }}</textarea>
      </div>
      <div class="request-box" v-if="apiData.requestParamsString.length>0">
        <div class="title">
          <span>请求体-RequestBody:</span>
        </div>
        <textarea class="my-textarea my-scroller body">{{ apiData.requestParamsString }}</textarea>
      </div>
      <div class="request-box">
        <div class="title">
          <span>响应-Response:</span>
        </div>
        <textarea class="my-textarea my-scroller response">{{ apiData.responseString }}</textarea>
      </div>
    </div>
  </el-dialog>
</template>

<script>
  import Tools from '../tools/Tools'

  export default {
    name: 'DebugApiDialog',
    data: function () {
      return {
        apiLoading: false,
        envUrl: '',
        allApiList: [],
        currApiUrl: '',
        apiGroupArr: [],
        dialogVisible: false,
        editor: null,
        apiData: {
          url: '',
          name: '',
          requestHeader: {Authorization: ''},
          requestBody: {username: '', password: ''},
          response: null,
          requestHeaderString: '',
          requestParamsString: '',
          responseString: ''
        }
      }
    },
    mounted () {
      let $this = this
      this.bus.$on('DebugApiDialogCallBus', function (apiData) {
        $this.allApiList = apiData.allApiList
        $this.apiGroupArr = Tools.handlerMenuArr($this.allApiList)
        $this.setApiData(apiData)
      })

      $this.envUrl = $this.currEnv.url
      $this.bus.$on('changeEnvCallBus', function (currEnv) {
        $this.envUrl = currEnv.url
        $this.$forceUpdate()
      })
    },
    methods: {
      // 切换api
      changeApi: function () {
        let $this = this
        var apiData = {}
        $this.allApiList.forEach(function (api, index) {
          if (api.url === $this.currApiUrl) {
            apiData = api
          }
        })
        $this.setApiData(Tools.handlerDebugApiData(apiData))
      },
      // 设置api数据
      setApiData: function (apiData) {
        this.currApiUrl = apiData.url
        this.apiData.name = apiData.name
        this.apiData.url = apiData.url
        this.apiData.requestBody = apiData.requestBody
        this.apiData.requestHeader = apiData.requestHeader
        this.apiData.response = ''
        this.dialogVisible = true
        if (this.apiData.requestHeader && JSON.stringify(this.apiData.requestHeader).length > 4) {
          this.apiData.requestHeaderString = Tools.meihuaJSON(JSON.stringify(this.apiData.requestHeader))
        } else {
          this.apiData.requestHeaderString = ''
        }
        if (this.apiData.requestBody && JSON.stringify(this.apiData.requestBody).length > 4) {
          this.apiData.requestParamsString = Tools.meihuaJSON(JSON.stringify(this.apiData.requestBody))
        } else {
          this.apiData.requestParamsString = ''
        }
        this.apiData.responseString = ''
        this.$forceUpdate()
      },
      // 发起请求
      request: function () {
        let $this = this
        let url = $this.envUrl + $this.apiData.url
        var params = {}
        var header = {}
        if ($this.apiData.requestParamsString.length > 0) {
          params = JSON.parse($this.apiData.requestParamsString)
        }
        if ($this.apiData.requestHeaderString.length > 0) {
          header = JSON.parse($this.apiData.requestHeaderString)
        }
        $this.apiLoading = true
        $this.MyRequest(url, params, function (data) {
          $this.apiLoading = false
          $this.apiData.response = data
          $this.apiData.responseString = Tools.meihuaJSON(JSON.stringify($this.apiData.response))
          $this.$forceUpdate()
        }, header,'post')
      }
    }
  }
</script>

<style scoped>
  .url-box {
    display: flex
  }

  .url-box span.api-name {
    padding: 0 10px;
    float: left;
    background: #334a6d;
    color: #FFFFFF;
    border-radius: 3px;
    text-align: center;
  }

  .url-box span.title {
    width: 60px;
    float: left;
    text-align: left;
    margin-left: 5px;
  }

  .url-box .url-value {
    width: 550px;
    text-indent: 5px;
    float: left;
    outline: 0;
  }

  .url-box .select-api {
    float: left;
    margin-left: 5px;
  }

  .url-box .select-api select {
    min-width: 200px;
    height: 25px;
  }

  .url-box .debug-button {
    width: 150px;
    float: left;
    margin-left: 10px;
  }

  .url-box .debug-button button {
    padding: 3px 10px;
  }

  .request-box {
    margin-top: 10px;
  }

  .request-box span {
    display: inline-block;
    background-color: #555555;
    color: #eeeeee;
    padding: 0px 10px;
    border-top-left-radius: 3px;
    border-top-right-radius: 3px
  }

  .my-textarea {
    width: 98%;
    min-height: 50px;
    outline: 0;
    border: 1px solid #AAAAAA;
    background: #f8f8f8;
    padding: 2px;
  }

  .my-textarea.header {
    min-height: 50px;
  }

  .my-textarea.body {
    min-height: 120px;
  }

  .my-textarea.response {
    min-height: 200px;
  }
</style>



