<template>
  <div class="right-content-item" v-if="apiData.url && apiData.url.length>10" :id="apiData.title">

    <!--接口名称-->
    <div class="title">
      <div :class="{'curr animated pulse':isAnimated}" style="display: inline-block">
        <span class="name" v-text="apiData.title"></span>
        <span class="url" v-text="apiData.url.substr(1)"></span>
      </div>
      <button class="button red debug-api-button" @click="openDebugDialogApi">请求示例</button>
    </div>

    <!--接口作者-->
    <div class="author-info">
      作者: demo001
    </div>

    <!--接口描述-->
    <div class="description-info" v-if="apiData.description && apiData.description.length>0" v-html="apiData.description"></div>

    <!--请求头-->
    <template v-if="apiData.header">
      <div class="sub-title">
        <i class="el-icon-caret-bottom"></i>
        <span>请求头部-RequestHeader</span>
      </div>
      <table class="my-table" border="1">
        <thead>
        <tr>
          <td class="table-field">字段</td>
          <td class="table-demo-value">示例值</td>
          <td class="table-description">描述</td>
          <td class="table-option">必填</td>
        </tr>
        </thead>
        <tbody>
        <tr v-for="(value,key) in apiData.header.fields.Header" :key="key">
          <td class="table-field" v-text="value.field"></td>
          <td class="table-demo-value">
            <input v-if="!(currEnv.url.length===0 && value.field==='${Authorization}')" type="text" :value="showValue(value.defaultValue)" readonly>
          </td>
          <td class="table-description" v-html="value.description"></td>
          <td class="table-option">
            <b class="font-red" v-if="!value.optional">*</b>
            <b class="font-grey" v-if="value.optional">-</b>
          </td>
        </tr>
        </tbody>
      </table>
    </template>

    <!--请求体-->
    <template v-if="apiData.parameter">
      <div class="sub-title">
        <i class="el-icon-caret-bottom"></i>
        <span>请求体-RequestBody</span>
      </div>
      <table class="my-table" border="1">
        <thead>
        <tr>
          <td class="table-field">字段</td>
          <td class="table-demo-value">示例值</td>
          <td class="table-description">描述</td>
          <td class="table-option">必填</td>
        </tr>
        </thead>
        <tbody>
        <tr v-for="(value,key) in apiData.parameter.fields.Parameter" :key="key">
          <td class="table-field" v-text="value.field"></td>
          <td class="table-demo-value">
            <input type="text" :value="value.defaultValue" readonly>
          </td>
          <td class="table-description" v-html="value.description"></td>
          <td class="table-option">
            <b class="font-red" v-if="!value.optional">*</b>
            <b class="font-grey" v-if="value.optional">-</b>
          </td>
        </tr>
        </tbody>
      </table>
    </template>


    <div class="sub-title">
      <i class="el-icon-caret-bottom"></i>
      <span>响应结果-Response</span>
    </div>
    <!--响应结果-->
    <pre v-html="apiData.success.examples[0].content"></pre>
  </div>
</template>

<script>
  import Tools from '../tools/Tools'
  export default {
    name: 'DocPageItem',
    props: {
      apiDataParams: Object,
      getAllApi: Function,
    },
    data: function () {
      return {
        isAnimated:false,
        envUrl: '',
        currEnv: {
          url:''
        },
        apiData: {}
      }
    },
    mounted () {
      let $this = this
      if ($this.$props.apiDataParams) {
        $this.apiData = $this.$props.apiDataParams
      }
      $this.bus.$on('changeEnvCallBus', function (currEnv) {
        $this.currEnv.url=''
        setTimeout(function () {
          $this.currEnv = currEnv
          $this.$forceUpdate()
        },500)
      })

      $this.bus.$on('turnApiHashCallBus', function (url) {
        if ($this.apiData.url===url){
          $this.isAnimated=true
          setTimeout(function () {
            $this.isAnimated=false
          },3000)
        }
      })
    },
    methods: {
      openDebugDialogApi: function () {
        var params=Tools.handlerDebugApiData(this.apiData)
        params.allApiList=this.$props.getAllApi()
        this.bus.$emit('DebugApiDialogCallBus',params)
      },
      showValue:function (value) {
        if (value==='${Authorization}'){
          return this.currEnv.Authorization
        }
        console.log(this.currEnv.Authorization)
        return value
      }
    }
  }
</script>

<style scoped>

</style>



