<template>
  <el-dialog
    :title="title"
    :visible.sync="dialogVisible"
    width="1100px" top="50px">
    <div class="my-scroller" style="width: 100%; height: 700px">
      <iframe v-if="dialogVisible" width="100%" :src="currUrl" frameborder="0" :height="height"></iframe>
    </div>
  </el-dialog>
</template>

<script>
  export default {
    name: 'WinDialog',
    data: function () {
      return {
        dialogVisible:false,
        title: '示例弹窗',
        height: 900,
        currUrl: 'http://www.baidu.com'
      }
    },
    mounted () {
      let $this=this
      this.bus.$on('openWinCallBus', function (params) {
        $this.dialogVisible=true
        $this.currUrl=params.url
        $this.height=params.height
        $this.title=params.title
        $this.$forceUpdate()
      })
    }
  }
</script>

<style scoped>

</style>



