      <li class="main-menu-item" v-for="(value,key) in menuArr" :key="key">
        <div class="title">
          <span v-text="value.groupName" @click="value.isVisibleList=!value.isVisibleList"></span>
<!--          <el-checkbox class="check-box" size="medium"></el-checkbox>-->
