              <a class="ovd_s"  :title="_value.name+'-'+_value.url.substr(1)" :href="'#'+_value.name" @click="checkApiUrl(_value.url)">
                <span class="name" v-text="_value.name"></span>
                <span class="url" v-text="_value.url.substr(1)"></span>
              </a>
            </li>
          </ul>
      </li>
      <!--<li class="main-menu-item">
        <div class="title">
          <span>登陆用户</span>
