    data: function () {
      return {
        currUrl: '',
        menuArr: [
          /*{
            groupName: '公共',
            isVisibleList: false,
            list: [
              {
                name: '登陆',
                url: 'Common/login',
              },
              {
                name: '刷新token',
                url: 'Common/reflushToken',
              },
            ],
          },
          {
            groupName: '登陆用户',
            isVisibleList: false,
            list: [
              {
                name: '基本信息',
                url: 'CurrUser/baseInfo',
              },
              {
                name: '更改密码',
                url: 'CurrUser/changePassword',
              },
              {
                name: '退出登陆',
                url: 'CurrUser/loginOut',
              },
            ],
          },*/
        ]
      }
    },
    mounted () {

    },
    methods:{
      setMenu:function (menuArr) {
        console.log('设置菜单数据')
        this.menuArr=menuArr
      },
      checkApiUrl:function (url) {
        this.bus.$emit('turnApiHashCallBus',url)
      }
    }
  }
</script>

