Vue.prototype.allProject = window.allProject
console.log('Vue.prototype.allProject',Vue.prototype.allProject)
Vue.prototype.apiList = []
Vue.prototype.currEnv = {
  name: '本地环境',
  key: 'local',
  url: 'http://fm.cc/index.php',
  Authorization: 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9..nCYlxc20ibrZIb7upQmlwZwifIR8O5QfjXBSatcJyRQ',
  default: true
}

Vue.MyRequestLoading = {}

/**
 * 通用请求方法
 * @param url 域名后的接口地址
 * @param params  参数
 * @param callback  正确-回调
 * @constructor
 */

Vue.use(VueResource)
Vue.MyRequestLoading = {}


/**
 * 通用请求方法
 * @param url 域名后的接口地址
 * @param params  参数
 * @param callback  正确-回调
 * @param header  请求头部
 * @param method  请求方法
 * @constructor
 */
Vue.prototype.MyRequest = function (url, params, callback,header,method) {
  var $this = this
  if (Vue.MyRequestLoading[url] === true) {
    return
  }
  if (typeof params === 'undefined' || params === null) {
    params = {}
  }
  if (!header){
    header={}
  }
  Vue.MyRequestLoading[url] = true
  $this.$http[method==='post'?'post':'get'](url, params, {
    emulateJSON: true,
    credentials: true,
    headers: header
  }).then(function (response) {
    Vue.MyRequestLoading[url] = false
    var res = response.data
    callback(res)
    // console.log(res)
  }, function (errorResponse) {
    Vue.MyRequestLoading[url] = false
    console.error('--------------------------http-请求错误--------------------------')
    console.log(JSON.stringify(errorResponse))
    callback('请求出错: '+JSON.stringify(errorResponse))
    $this.$message.error(JSON.stringify(errorResponse))
  })
}

let bus = new Vue()
Vue.prototype.bus = bus

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: {App},
  template: '<App/>',
  beforeMount: function () {
    Vue.prototype.bus = this
  }
})



