import Vue from 'vue'
import Router from 'vue-router'
import DocPage from '@/components/page/DocPage'
import Home from '@/components/page/Home'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/DocPage',
      name: 'DocPage',
      component: DocPage
    }
  ]
})



