<script>
  export default {
    name: 'Tools',
    // json格式化,增加缩进
    meihuaJSON: function (json) {
      // json替换方法
      function repeat (s, count) {
        return new Array(count + 1).join(s)
      }

      var tab = '    '
      var targetJson = ''
      var indentLevel = 0
      var inString = false
      var currentChar = null
      for (var i = 0, len = json.length; i < len; i += 1) {
        currentChar = json.charAt(i)
        switch (currentChar) {
          case '{':
          case '[':
            if (!inString) {
              targetJson += currentChar + '\n' + repeat(tab, indentLevel + 1)
              indentLevel += 1
            } else {
              targetJson += currentChar
            }
            break
          case '}':
          case ']':
            if (!inString) {
              indentLevel -= 1
              targetJson += '\n' + repeat(tab, indentLevel) + currentChar
            } else {
              targetJson += currentChar
            }
            break
          case ',':
            if (!inString) {
              targetJson += ',\n' + repeat(tab, indentLevel)
            } else {
              targetJson += currentChar
            }
            break
          case ':':
            if (!inString) {
              targetJson += ': '
            } else {
              targetJson += currentChar
            }
            break
          case ' ':
          case '\n':
          case '\t':
            if (inString) {
              targetJson += currentChar
            }
            break
          case '"':
            if (i > 0 && json.charAt(i - 1) !== '\\') {
              inString = !inString
            }
            targetJson += currentChar
            break
          default:
            targetJson += currentChar
            break
        }
      }
      return targetJson
    },
    // 处理-传递给【左侧菜单】数据
    handlerMenuArr: function (apiList) {

      var winHash = window.location.hash
      if (winHash.length>0){
        winHash=winHash.substr(1)
      }else{
        winHash=''
      }
      var groupArr = []
      var groupNameArr = []
      apiList.forEach(function (api, index) {
        if (groupNameArr.indexOf(api.group) === -1) {
          groupNameArr.push(api.group)
          groupArr.push({
            groupName: api.group,
            isVisibleList: false,
            list: []
          })
        }
      })
      groupArr.forEach(function (group, index) {
        apiList.forEach(function (api, _index) {
          if (api.group === group.groupName) {
            if (winHash && winHash === api.title) {
              groupArr[index].isVisibleList=true
            }
            var arr=[];
            group.list.push({
              name: api.title,
              url: api.url,
            })
          }
        })
      })

      groupArr.forEach(function (group,index) {
        var arr=[];
        var newArr=[];
        var map=[];
        group.list.forEach(function (api,_index) {
          arr.push(api.name)
          map[api.name]=api
        })
        arr.sort()
        arr.forEach(function (name,_index) {
          newArr.push(map[name])
        })
        group.list=newArr
      })
      return groupArr
    },
    // 处理-传递给【api-调试弹窗】数据
    handlerDebugApiData: function (apiData) {
      var requestBody = {}
      if (apiData.parameter) {
        apiData.parameter.fields.Parameter.forEach(function (value, index) {
          requestBody[value.field] = value.defaultValue
        })
      }
      var requestHeader = {}
      if (apiData.header) {
        apiData.header.fields.Header.forEach(function (value, index) {
          requestHeader[value.field] = value.defaultValue
        })
      }
      return {
        name: apiData.title,
        url: apiData.url,
        requestHeader: requestHeader,
        requestBody: requestBody
      }
    }
  }
</script>

