<template>
  <div id="doc-page"
       v-loading="pageLoading"
       element-loading-text="拼命加载中"
       element-loading-spinner="el-icon-loading"
       element-loading-background="rgba(0, 0, 0, 0.8)">
    <div id="top-bar">
      <div id="project-name">
        <a class="home" href="/">
          <i class="el-icon-s-shop"></i>
        </a>
        <span class="title" v-text="projectData.name"></span>
      </div>
      <div id="right-buttons">
        <a class="item button blue" target="_blank" @click="openWin(apiProject.requestSignDescriptionUrl,'Request签名',570)">Request签名</a>
        <a class="item button blue"  target="_blank" @click="openWin(apiProject.responseDescriptionUrl,'Response解释',900)">Response解释</a>
        <span class="item blue">环境切换
          <select @change="changeEnv($event)">
            <option v-for="(value,key) in apiProject.envList" :key="key" :value="value.key"
                    :selected="value.key===currEnv.key?'selected':''" v-text="value.name"></option>
          </select>
        </span>
      </div>
    </div>
    <left-menu ref="leftMenu"/>
    <div id="right-content" class="my-scroller">
      <doc-page-item v-for="(value,key) in apiList" :key="key" :api-data-params="value" :get-all-api="getApiList"/>
    </div>
  </div>
</template>

<script>
  import LeftMenu from '../box/LeftMenu'
  import DocPageItem from '../box/DocPageItem'
  import Tools from '../tools/Tools'

  export default {
    name: 'DocPage',
    components: {DocPageItem, LeftMenu},
    data: function () {
      return {
        pageLoading: false,
        apiProject: {},
        projectData: {
          name: '',
          serverKey: '',
          apiDataUrl: '',
          apiProjectUrl: ''
        },
      }
    },
    mounted () {
      let $this = this
      var projectData = {}
      $this.allProject.forEach(function (value, index) {
        if ($this.$route.query.serverKey === value.serverKey) {
          projectData = value
        }
      })
      $this.projectData = projectData
      $this.loadApiList()
      $this.loadProject()
      $this.$forceUpdate()

    },
    methods: {
      // 加载-服务api数据
      loadApiList: function () {
        let $this = this
        $this.MyRequest($this.projectData.apiDataUrl, {}, function (apiList) {
          var newApiList=Tools.handlerMenuArr(apiList)
          var sortList=[]
          newApiList.forEach(function (group,index) {
            group.list.forEach(function (api,_index) {
              apiList.forEach(function (oldApi,oldIndex) {
                if (oldApi.title===api.name){
                  sortList.push(oldApi)
                }
              })
            })
          })
          apiList=sortList
          $this.apiList = apiList
          $this.allApiList = apiList
          $this.$refs.leftMenu.setMenu(newApiList)
          $this.$forceUpdate()
        })
      },
      // 加载-服务数据
      loadProject: function () {
        let $this = this
        $this.pageLoading = true
        $this.MyRequest($this.projectData.apiProjectUrl, {}, function (apiProject) {
          $this.apiProject = apiProject
          var envParams = {}
          $this.pageLoading = false
          $this.apiProject.envList.forEach(function (value, key) {
            if (value.default === true) {
              envParams = value
            }
          })
          $this.currEnv = envParams
          $this.bus.$emit('changeEnvCallBus',$this.currEnv)
          $this.$forceUpdate()
        },null,'get')
      },
      // 更改环境
      changeEnv: function (e) {
        let $this = this
        var envData = {}
        $this.apiProject.envList.forEach(function (value, key) {
          if (value.key === e.target.value) {
            envData = value
          }
        })
        $this.currEnv = envData
        $this.bus.$emit('changeEnvCallBus', envData)
        $this.$forceUpdate()
      },
      getApiList: function () {
        return this.apiList
      },
      openWin:function (url,title,height) {
        this.bus.$emit('openWinCallBus', {
          title:title,
          url:url,
          height:height,
        })
      }
    }
  }
</script>



