<template>
  <div class="main-box">
    <template v-if="allProject.length>0">
      <router-view/>
      <debug-api-dialog/>
      <win-dialog/>
    </template>
  </div>
</template>

<script>
  import DebugApiDialog from './components/dialog/DebugApiDialog'
  import WinDialog from './components/dialog/WinDialog'
  import Utils from './components/tools/Utils'

  export default {
    name: 'App',
    components: {WinDialog, DebugApiDialog},
    mounted () {
      if (this.allProject.length===0){
        this.inputProjectData()
      }
    },
    methods:{
      inputProjectData:function () {
        this.$prompt('请输入项目apidoc配置json', '系统提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
        }).then(({ value }) => {
          // let encrypString=Utils.encrypt(value)
          // console.log('encrypString: '+encrypString)
          localStorage.setItem('currAllProject', value)
          this.$message.success("设置成功!")
          location.reload()
        }).catch(() => {
        });
      }
    }
  }
</script>
<style>
  /**引入共样式文件**/
  @import 'assets/css/com.css';
  @import 'assets/css/button.css';
  @import 'assets/css/custom.css';
  /*@import 'assets/font/iconfont.css';*/
  @import 'assets/css/animate.css';
  @import 'assets/css/elememt-ui-custom.css';
</style>



