<template>
    <div id="home-page">
      <h2>账房-接口文档</h2>
      <div class="home-nav">
        <router-link
        class="item"
        v-for="(value,key) in allProject"
        :key="key"
        v-text="value.name"
        :to="'/DocPage?serverKey='+value.serverKey"
      />
      </div>
    </div>
</template>

<script>
  export default {
    name: 'Home'
  }
</script>

<style scoped>
  #home-page h2{ text-align: center; font-size: 22px; padding-top: 5%}
  #home-page .home-nav{ text-align: center; margin-top: 50px;}
  #home-page .home-nav .item{ margin-left: 10px; font-size: 14px; cursor: pointer; display: inline-block; width: 120px; line-height: 40px; height: 40px; background-color: #266486; color: #FFFFFF}
  #home-page .home-nav .item:active{ opacity: 0.9}
</style>



